import React, { useEffect } from 'react'
import { Box, Grid, Typography } from '@mui/material'
import { useLocation, useNavigate } from 'react-router-dom'
import Chart from './Chart/Chart'
import Styled from './style'

const Result = () => {
  const location = useLocation()
  const navigate = useNavigate()

  const isEmpty = (obj) => {
    return Object.keys(obj).length === 0
  }

  useEffect(() => {
    if (!location?.state?.result || isEmpty(location?.state?.result)) {
      navigate('/')
    }
  }, [location])

  return (
    <Box maxWidth={550} sx={{ marginLeft: 'auto', marginRight: 'auto' }}>
      <Box display="flex" alignItems="center" justifyContent="space-between">
        <Box>
          <Typography variant="h5" mb={1} fontWeight={600}>
            Meet your result
          </Typography>
          <Typography variant="body1">
            {location?.state?.profession?.professionName}
          </Typography>
        </Box>
      </Box>
      {Object.keys(location?.state?.result).map((keyName) => (
        <Box mt={4}>
          <Styled.ResultWrapper>
            <Typography variant="body1" fontWeight={600}>
              {keyName}
            </Typography>
            <Chart data={location?.state?.result[keyName]} />
            <Box>
              <Grid container>
                <Grid item xs={6}>
                  <Typography variant="body2" fontWeight={600}>
                    Strengths
                  </Typography>
                  <Box component="ul" sx={{ padding: 0, mt: 2, mb: 0 }}>
                    {Array.from({ length: 5 }).map((_, indx) => (
                      <Box
                        // eslint-disable-next-line react/no-array-index-key
                        key={indx}
                        component="li"
                        sx={{ listStylePosition: 'inside', mb: 1 }}
                      >
                        You are user-centric, valuing the input of your users to
                        create designs that truly meet their needs.
                      </Box>
                    ))}
                  </Box>
                </Grid>
                <Grid item xs={6}>
                  <Typography variant="body2" fontWeight={600}>
                    Worth to improve
                  </Typography>
                  <Box component="ul" sx={{ padding: 0, mt: 2, mb: 0 }}>
                    {Array.from({ length: 5 }).map((_, indx) => (
                      <Box
                        // eslint-disable-next-line react/no-array-index-key
                        key={indx}
                        component="li"
                        sx={{ listStylePosition: 'inside', mb: 1 }}
                      >
                        You are user-centric, valuing the input of your users to
                        create designs that truly meet their needs.
                      </Box>
                    ))}
                  </Box>
                </Grid>
              </Grid>
            </Box>
          </Styled.ResultWrapper>
        </Box>
      ))}
    </Box>
  )
}

export default Result
